.App {
  text-align: center;
  height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #101013;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.trading-btns {
  margin: 0 5px;
  background: black;
  color: white;
  width: 80px;
}


.font-white {
  color: white;
}

.trading-setting {
  margin: 5px;
  /* display: flex; */
}

.trading-symbol {
  margin: 0 15px 0 0;
  background: black;
  color: white;
  width: 200px;
}

.trading-leverage {
  margin: 0 0 0 0;
  background: gray;
  color: black;
  width: 150px;
}

.WatchListBox {
  padding: 5px;
  height: 410px;
  background-color: black;
  overflow: hidden;
}


.TableContainer {
  height: calc(100% - 70px); /* Adjust height based on other UI elements like filters */
  overflow-y: auto; /* Enable vertical scrolling */
  background-color: black;
}
.btn_logout {
  margin: 5px 45px 0 0;
  background-color: rgb(40, 40, 40);
  color: white;
  width: 80px;
  height: 40px;
  border-radius: 10px;
  float: right;
}
.btn_logout :hover{
  background-color: rgb(100,100,100);
}

.account-switch {
  margin: 5px 45px 0 0;
  background: #3681d7;
  color: rgb(223, 223, 223);
  width: 200px;
  height: 40px;
  border-radius: 10px;
  float: right;
}

.loading-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.circular-progress {
  width: 100px !important;
  height: 100px !important;
}
